.screen-page {
  width: 100%;
  min-height: 100vh;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  display: flex;
  align-items: stretch;
  justify-content: center;
}

.screen-page.gaming {
  animation: fade-in-bg 1s ease-in-out forwards;
}

@keyframes fade-in-bg {
  from { opacity: 0; }
  to { opacity: 1; }
}

