@font-face {
  font-family: 'FZCuHeiSong-B-JF';
  src: url('../../assets/FZCuHeiSong-B-JF.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

.game-page {
  min-height: 100vh;
  padding: 48px 32px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  gap: 24px;
  align-items: flex-start;
  justify-content: center;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.8), #c6d8ff);
  color: #1f2a44;
  position: relative;
}

.game-page-waiting {
  align-items: center;
  justify-content: center;
}


.primary-btn {
  width: 898px;
  height: 205px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.waiting-others-btn {
  width: 898px;
  height: 205px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-image: url('../../assets/waiting_others.png');
  background-size: 100% 100%;
  background-position: center;
  background-repeat: no-repeat;
}

.hint {
  margin-top: 12px;
  font-size: 18px;
  color: #334155;
}


.game-demo-video {
  position: absolute;
  top: 265px;
  left: 50%;
  transform: translateX(-50%);
  width: 1400px;
  height: 723px;
  pointer-events: none;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.wait-for-photo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  height: auto;
  user-select: none;
}

.election-method-text {
  position: absolute;
  width: 540px;
  height: 97px;
  /* left: 840px; */
  left: 50%;
  transform: translateX(-50%);
  top: 100px;
  font-family: 'FZCuHeiSong-B-JF';
  font-style: normal;
  font-weight: 400;
  font-size: 80px;
  line-height: 97px;
  text-align: center;
  color: #000000;
}

.camera-video {
  position: absolute;
  width: 685px;
  height: 685px;
  left: 50%;
  transform: translateX(-50%);
  top: 280px;
  object-fit: cover;
  border-radius: 8px;
}

.captured-photo {
  position: absolute;
  width: 685px;
  height: 685px;
  left: 50%;
  transform: translateX(-50%);
  top: 280px;
  object-fit: cover;
  border-radius: 8px;
  transition: border-radius 1s ease-in-out;
  border: 6px solid #000;
}

.captured-photo-circle {
  border-radius: 50%;
}

.gaming-photo-anim {
  animation: roundThenFly 1s ease-in-out forwards;
  will-change: transform, border-radius;
}

@keyframes roundThenFly {
  0% {
    border-radius: 8px;
    transform: translate(-50%, 0);
  }

  33.333% {
    border-radius: 50%;
    transform: translate(-50%, 0);
  }

  100% {
    border-radius: 50%;
    transform: translate(-50%, -120vh);
  }
}

.gaming-center-text {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 1600px;
  text-align: center;
  font-family: 'FZCuHeiSong-B-JF';
  font-style: normal;
  font-weight: 400;
  font-size: 120px;
  line-height: 146px;
  color: #3C0505;
  white-space: pre-line;
  opacity: 0;
}

.gaming-center-text-show {
  animation: gamingCenterFadeIn 2s ease-in forwards;
}

@keyframes gamingCenterFadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.photo-controls {
  position: absolute;
  left: 340px;
  top: 980px;
  display: flex;
  gap: 20px;
  justify-content: center;
}

.photo-btn {
  position: absolute;
  left: 1681px;
  top: 863px;
  width: 132px;
  height: 132px;
  border: none;
  transition: all 0.3s;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  font-family: 'FZCuHeiSong-B-JF';
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 39px;
  text-align: center;
  color: #000000;
  display: flex;
  align-items: center;
  justify-content: center;
}

.complete-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  object-fit: cover;
}

.countdown-display {
  position: absolute;
  left: 1624px;
  top: 51px;
  font-family: 'FZCuHeiSong-B-JF';
  font-style: normal;
  font-weight: 400;
  font-size: 40px;
  line-height: 49px;
  color: #000000;
}

.connecting-hint {
  position: fixed;
  right: 16px;
  bottom: 12px;
  font-size: 14px;
  color: #475569;
  opacity: 0.8;
}

.complete-date {
  position: absolute;
  left: 1130px;
  top: 694px;
  font-family: 'FZCuHeiSong-B-JF';
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 21px;
  color: #000000;
}

.password-input-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 10000;
}